.dropdowni {
  position: relative;
  display: inline-block;
}

.dropdown-contenti {
  display: none;
  position: absolute;
  background-color: #f6f6f6;
  min-width: 300px;
  overflow: auto;
  border-radius: 0px 0px 10px 10px;
  border: 1px solid #1d4480;
  /* box-shadow: 2px 2px 0px 0px #1d4480 !important; */
  z-index: 1000; /* Higher z-index compared to other elements */
  height: 300px;
  margin-top: 23px;
  margin-left: 50px;
}
.dropdown-contenti::-webkit-scrollbar {
  width: 0.6em;
  border-radius: 0px 0px 10px 10px;
}

.dropdown-contenti::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  border-radius: 0px 0px 10px 10px;
}

.dropdown-contenti::-webkit-scrollbar-thumb {
  background-color: darkgrey;
  outline: 1px solid slategrey;
  border-radius: 0px 0px 10px 10px;
}
.dropdown-contenti a {
  color: black;
  padding: 12px 0px;
  text-decoration: none;
  display: block;
  z-index: 4; /* Higher z-index for dropdown items */
  padding-left: 7%;
}

.dropdowni a:hover {
  background-color: #ddd;
}

.showi {
  display: block;
}

